import { formatNumber } from "./transformDataForTables";
import { changeLang } from "../change-lang";

export function transformTotalStatData(arr, flag, numOfSellers) {
  let resultArray;

  if (flag === 'shop') {
    resultArray = [
      {
        title: ' , ',
        value: arr[0].selled_amount,
      },
      {
        title: ', UZS',
        value: arr[0].revenue,
      },
      {
        title: ' ',
        value: numOfSellers?.num_of_categories || changeLang(' '),
      },
      {
        title: ' ',
        value: arr[0].avg_bill,
      }
    ];
  }

  if (flag === 'category') {
    const obj = {
      orders_amount_per_day: 0,
      revenue: 0,
      available_product: 0,
      sellers_count: numOfSellers ? numOfSellers.num_of_sellers : changeLang(' '),
    };

    arr.forEach(item => {
      obj['orders_amount_per_day'] += +item.orders_amount_per_day;
      obj['revenue'] += +item.revenue;
      obj['available_product'] += +item.available_sku;
    });

    resultArray = [
      {
        title: ' ',
        value: arr.length,
      },
      {
        title: ' ',
        value: obj.orders_amount_per_day,
      },
      {
        title: ', UZS',
        value: formatNumber(obj.revenue),
      },
      {
        title: ' ',
        value: obj.sellers_count,
      },
      {
        title: ' ',
        value: numOfSellers ? numOfSellers.num_of_distinct_sku : changeLang(' '),
      },
    ];
  }

  return resultArray;
}
